import {ref, onMounted} from '@vue/composition-api';
import FileService from '@/api/file';

const Transformer = {
    file: arr => {

        return arr && arr.map(item => ({
            size: item.size, // 
            filePath: item.filePath, //  ( absolute )
            relativePath: item.relativePath, //  ( relative )
            name: item.name, // 
            createTime: item.createTime, // 
            modifyTime: item.modifyTime, // 
            date: item.modifyTime, // 
            fileType: item.fileType, // 
            type: item.fileType, // 
        }))
    }
}

const useFile = () => {

    const tableData = ref([]);
    const currPath = ref('/');

    const getTableData = (directory) => {

        currPath.value = directory;

        //  & 
        FileService.listFile(directory)
            .then(files => {

                tableData.value = Transformer.file(files);
            })
            .catch(console.error)
    };

    onMounted(getTableData);

    return {
        tableData,
        currPath,
        getTableData
    }
}

export default useFile();